<template>
    <div class="container">
        <!-- 中国地图展示 -->
        <div id="mapBox" class="centerItem"></div>
        <gio-earth class="earthItem"/>

        <g-modal
            v-if="isModalShow"
            :title="modalTitle"
            @close="close"/>

        <Button @click="link" class="btn">数据面板</Button>
    </div>

</template>

<script>
    import { geoCoordMapD, HFDataD, HongKong } from './data'
    import GioEarth from '../components/GioEarth'
    import gModal from './modal'

    export default {
        components: { GioEarth, gModal },
        data: ()=>({
            isModalShow: false,
            modalTitle: '',
        }),
        mounted() {
            // 基于准备好的dom，初始化echarts实例
            var mapBoxEchart = echarts.init(document.getElementById('mapBox'));

            var geoCoordMap = geoCoordMapD
            var HFData = HFDataD
            var planePath = 'image:///shoes.svg'; // 箭头的svg

            // push进去线路开始-结束地点-经纬度
            var convertData = function(data) {
                var res = [];
                for (var i = 0; i < data.length; i++) {
                    var dataItem = data[i];
                    var fromCoord = geoCoordMap[dataItem[0].name];
                    var toCoord = geoCoordMap[dataItem[1].name];
                    if (fromCoord && toCoord) {
                        res.push([{
                            coord: fromCoord
                        }, {
                            coord: toCoord
                        }]);
                    }
                }
                return res;
            };
            var color = ['red', '#FFFFA8', '#46bee9']; // 圆圈和字的颜色，线的颜色，箭头颜色
            // 数据
            var series = [];
            // 遍历由合肥到其他城市的线路
            [
                ['合肥', HFData],
                ['香港', HongKong]
            ].forEach(function(item, i) {
                // 配置
                series.push(
                    {
                        // 路程中的线条；系列名称，用于 tooltip 的显示
                        name: item[0],
                        type: 'lines',
                        zlevel: 1, // 用于 Canvas 分层，不同 zlevel 值的图形会放置在不同的 Canvas 中
                        // lineStyle出发到目的地 的线条颜色
                        lineStyle: {
                            normal: {
                                color: color[i],
                                width: 1,
                                curveness: 0.2 //幅度
                            }
                        },
                        data: convertData(item[1]) //开始到结束数据
                    },
                    {
                        // 移动的物体；出发地信息
                        name: item[0],
                        type: 'lines',
                        zlevel: 2,
                        effect: {
                            show: true,
                            period: 6,
                            trailLength: 0,
                            symbol: planePath,
                            symbolSize: 18
                        },
                        lineStyle: {
                            normal: {
                                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                    offset: 0,
                                    color: '#FFFFA8' // 出发
                                }, {
                                    offset: 1,
                                    color: '#58B3CC' // 结束 颜色
                                }], false),
                                width: 1.5,
                                opacity: 0.4,
                                curveness: 0.2
                            }
                        },
                        data: convertData(item[1])
                    },
                    {
                        // 目的地的样式；目的地信息
                        name: item[0],
                        type: 'effectScatter',
                        coordinateSystem: 'geo',
                        zlevel: 2,
                        rippleEffect: {
                            brushType: 'stroke'
                        },
                        label: {
                            normal: {
                                show: true,
                                position: 'right',
                                formatter: '{b}'
                            }
                        },
                        symbolSize: function(val) {
                            return val[2] / 8;
                        },
                        itemStyle: {
                            normal: {
                                color: color[i]
                            }
                        },
                        data: item[1].map(function(dataItem) {
                            return {
                                name: dataItem[1].name,
                                value: geoCoordMap[dataItem[1].name].concat([dataItem[1].value])
                            };
                        })
                    }
                );
            });
            console.log('series', series)
            // 指定相关的配置项和数据
            var mapBoxOption = {
                geo: {
                    map: 'china',
                    roam: false, // 是否开启鼠标缩放和平移漫游。默认不开启。如果只想要开启缩放或者平移，可以设置成 'scale' 或者 'move'。设置成 true 为都开启
                    aspectScale: 0.75,
                    zoom: 1.20,
                    label: {
                        normal: {
                            show: true,
                            textStyle: {
                                color: '#00a0c9'
                            }
                        },
                        emphasis: { // 对应的鼠标悬浮效果
                            show: false,
                            textStyle: {
                                color: "#00a0c9"
                            }
                        }
                    },
                    itemStyle: {
                        normal: {
                            areaColor: '#0083ce', // 地图颜色
                            borderColor: 'black' // 省分割线
                        },
                        emphasis: {
                            borderWidth: 1,
                            borderColor: '#0066ba',
                            areaColor: "#0494e1",
                            shadowColor: 'rgba(0, 0, 0, 0.5)'
                        }
                    }
                },
                series: series
            };
            // 使用制定的配置项和数据显示图表
            mapBoxEchart.setOption(mapBoxOption);
            // echart 图表自适应
            window.addEventListener("resize", function() {
                mapBoxEchart.resize();
            });

            mapBoxEchart.on('click',(params)=> {
                console.log(params)
                this.modalTitle = params.name
                this.isModalShow = true
            });


        },
        methods: {
            close(){
                this.isModalShow = false
            },
            onClick(){
                console.log('index')
                this.$route.push('index')
            },
            link() {
                this.$router.push('/mobile/index')
            },
        }
    }
</script>

<style lang="scss" scoped>
    .container {
        display: flex;
        flex-direction: column;
        height: 100%;
    }

    .centerItem {
        display: none;
        width: 100%;
        height: 50%;
        background-color: #003077;
        margin: 110px auto 0 auto;
    }

    .earthItem {
        width: 100%;
        height: 100%;
        background-image: url("https://du.hupucdn.com/FrJ5HPXRsYWU9hABzS4nYTJ5sasG");
        background-size: cover;

    }
    .btn {
        background-color: #454d66;
        position: absolute;
        height: 60px;
        margin-top: 60px;
        left: 0;
        bottom: 0;
        width: 100%;
        border: none;
        color: #eee;
        font-size: 18px;
    }
</style>
